body{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 200;
}
ul{
    text-decoration: none;
}

/* HEADER */
header{
    display: flex;
    justify-content: center;
}
header h1 {
    text-align: center;
    margin: 0px;
    padding:60px 20px 60px 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 200;
    letter-spacing: 3px;
    font-size: 50px;
    color:white;
}

/* NAVBAR */
.nav {
    display: flex;
    justify-content: flex-end;
    padding-right:5px
}
.nav-link {
    color: white;
    letter-spacing: 1.5px;
    font-size: 20px;
}
.nav-item{
    padding:0px 8px 0px 8px
}
.nav-item :hover{
    background-color: rgb(247, 195, 25);
}

.nav-tabs :hover{
    color: rgb(241, 42, 165);
    
}

/* PAGES */
.contentpages {
    padding:40px;
    text-align: center;
}

/* ABOUT PAGE */
.aboutlinks { 
    text-decoration: none; 
    color: rgb(241, 42, 165);
    font-weight: 400;
    border-bottom:solid thin rgb(241, 42, 165)
}

/* PORTFOLIO PAGE */
.portpiecetitle{
    font-size: 28px;
    margin:25px 0px
}
.portpiecerepo{
    font-size: 17px;
    text-decoration: none;
    font-style: italic;
    color: white;
    background-color:rgb(241, 42, 165);
    padding: 8px;
}
.portpiecerepo:hover{
    background-color: rgb(247, 195, 25);
    color: black;
}
.portfoliopieces {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.portfoliopiece :hover{
    filter: brightness(90%);
    box-shadow: 1px 1px 16px 8px rgba(131, 130, 130, 0.5);
    transform: scale(1.14)
}

/* CONTACT PAGE*/
.contactme2{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    padding: 70px;
    margin:50px;
    background-color:#FFC6C6;
}
.contactme2 h2{
    text-align:center;
    line-height: 1.2;
    letter-spacing: 2px;
    font-size: 40px;
    font-weight: 400;
    padding: 30px;
    color: rgb(241, 42, 165);
    border-bottom: solid thin rgb(241, 42, 165);
}
.contactme2a, .contactme2p{
    font-size:30px;
    font-weight: 200;
    color: rgb(241, 42, 165);
    padding:20px;
}

.contactcon {
    display: flex;
    flex-wrap: wrap;
}
.contactme2a:hover {
    text-decoration: underline solid white;
    color:white;

}

/* RESUME PAGE */
.resume{
    padding:45px;
    text-align: center;
    margin:10px 20px 40px 20px;
    display: flex;
    justify-content: space-around;
    
}
.resume ul{
    text-decoration: none;
}
.resume h1{
    padding: 20px;
}
.resume p {
    margin: 0px;
    font-size: 20px;
}
.prof{
    font-style: italic;
    text-decoration: underline solid;
}

/* FOOTER */
.footer{
    font-size: 15px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 300;
    border-top: thin solid rgb(228, 228, 228);
    letter-spacing: 2px;
    color:lightgrey;
}

.footer p{
    padding-top:10px;
    margin:0px
}
.footerlogos {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}


/* MEDIA QUERIES */

@media screen and (max-width:505px){
    .nav{
    flex-direction: column;
    }
    .nav li  {
        text-align: center;
        padding: 10px;
        
    }
    .resume {
        flex-direction: column;
    }
}
@media screen and (max-width:360px){
  header h1{
      font-size: 45px;
  }
}